<template>
    <div class='warp'>
       <router-link :to='{path:"/img",query:{id:detaiList.SerialID}}' class='img_box'>
           <img :src="detaiList.CoverPhoto"> 
           <span class='imgs'>{{detaiList.pic_group_count}}张图片</span>
       </router-link>
       <div class='des_box'>
           <div class='left'>
               <span class='prices'>{{detaiList.market_attribute&&detaiList.market_attribute.dealer_price}}</span>
               <p>指导价{{detaiList.market_attribute&&detaiList.market_attribute.official_refer_price}}</p>
           </div>
           <router-link to='/follor' class='right'>
               <span>{{detaiList.BottomEntranceTitle}}</span>
           </router-link>
       </div>
      <section>
          <div class='type_box'>
            <span @click="changeYear(item)" v-for="(item, index) in years" :key="index" :class="currentYear==item?'active':''">{{item}}</span>
          </div>
          <div class='list_box'>
              <ul v-for='(item,index) in lists' :key='index'>
                  
                  <h6>{{index}}</h6>
                  <li v-for='(val,key) in item' :key='key' class='box'>
                      <p>{{val.market_attribute.year+'款 '+val.car_name}}</p>
                      <span>{{`${val.max_power}马力${val.gear_num}档${val.trans_type}`}}</span>
                      <p class='price_box'>
                            <span>指导价{{`${val.market_attribute.dealer_price_min}`}}</span>
                            <span class='color'>{{val.market_attribute.official_refer_price}}</span>
                      </p>
                      <p class='float_box'>询问底价</p>
                  </li>
                   
              </ul>
          </div>
      </section>
     <div class='footer'>
        <p>询问底价</p>
        <span>本地经销商为你报价</span>
     </div>
    </div>
</template>
<script>
    import {
        mapState,
        mapMutations,
        mapActions,
        mapGetters
    } from 'vuex'
    export default {
        computed: {
            ...mapState({
                detaiList: state => state.detail.detaiList,
                currentYear: state => state.detail.currentYear
            }),
            ...mapGetters({
                years: 'detail/years',
                lists: 'detail/lists'
            })
        },
        methods: {
            ...mapMutations({

            }),
            ...mapMutations({
                changeYear: 'detail/changeYear'
            }),
            ...mapActions('detail', ['DatailLists']),
            updata() {
                let id = this.$route.query.id;
                this.DatailLists(id).then(res => {
                    console.log(id)
                })
            }
        },
        mounted() {
            this.updata();
            //console.log(this.DatailLists())
            console.log(this.$route.query.id)

        },
    }
</script>
<style scoped lang='scss'>
    .warp {
        width: 100%;
        height: 100%;
        overflow: scroll;
        background: #eee;
    }
    
    .img_box {
        width: 100%;
        box-sizing: border-box;
        background: #fff;
        position: relative;
        img {
            width: 100%;
            height: auto;
        }
        .imgs {
            position: absolute;
            right: .2rem;
            bottom: .5rem;
            font-size: .28rem;
            color: #fff;
            padding: .1rem .3rem;
            background: rgba(0, 0, 0, .6);
            border-radius: .5rem;
        }
    }
    
    .des_box {
        width: 100%;
        display: flex;
        background: #fff;
        padding: .2rem;
        margin-top: -1rem;
        .left {
            flex: 1;
            /* padding: .1rem; */
            .prices {
                color: red;
                font-size: .28rem;
                margin-top: .2rem;
                position: relative;
            }
            p {
                color: #666;
                font-size: .28rem;
            }
        }
        .right {
            flex: 1;
            line-height: 2;
            span {
                padding: .1rem .8rem;
                background: #00afff;
                font-size: .28rem;
                text-align: center;
                color: #fff;
                border-radius: .2rem;
            }
        }
    }
    
    section {
        width: 100%;
        height: 100%;
        .type_box {
            width: 100%;
            height: 1rem;
            margin-top: .2rem;
            background: #fff;
            line-height: .5rem;
            span {
                font-size: .28rem;
                padding: 0 .3rem;
            }
        }
        .list_box {
            width: 100%;
            height: 6rem;
            ul {
                h6 {
                    font-size: .36rem;
                    font-weight: 400;
                    padding: .1rem;
                    color: #999;
                }
                .box {
                    line-height: 2;
                    font-size: 0;
                    background: #fff;
                    position: relative;
                    p {
                        font-size: .36rem;
                        padding: 0 .1rem;
                        color: #3d3d3d;
                    }
                    span {
                        font-size: .28rem;
                        color: #bdbdbd;
                        padding: 0 .1rem .1rem .1rem;
                    }
                    .price_box {
                        text-align: right;
                        border-bottom: 1px solid #ccc;
                        span {
                            color: #818181;
                        }
                        .color {
                            color: red
                        }
                    }
                    .float_box {
                        text-align: center;
                        color: #00afff
                    }
                    .bootom_box {
                        text-align: center;
                        color: #00afff;
                    }
                }
            }
        }
    }
    
    .footer {
        position: fixed;
        width: 100%;
        bottom: 0;
        left: 0;
        background: #3aacff;
        text-align: center;
        color: #fff;
        font-size: .28rem;
        line-height: 1.5;
    }
    
    .type_box .active {
        color: #3aacff;
    }
</style>


